import * as Yup from 'yup';

export let signUpSchema = Yup.object({
	email: Yup.string()
						.email(' email-')
						.required('  '),
	password: Yup.string()
							.min(4, ' 4 ')
							.required('  '),
	clientId: Yup.string(),
	firstName: Yup.string(),
	lastName: Yup.string(),
	approved: Yup.boolean()
});


export let signInSchema = Yup.object({
	email: Yup.string()
					.email('  email')
					.required('  '),
	password: Yup.string()
							.min(4, '  ')
							.required('  '),
});


export let newEemployeeSchema = Yup.object({
	email: Yup.string()
						.email(' email-')
						.required('  '),
	password: Yup.string()
							.min(4, ' 4 ')
							.required('  '),
	firstName: Yup.string(),
	lastName: Yup.string(),
	approved: Yup.boolean()
});


export let employeeDetailsSchema = Yup.object({
	password: Yup.string()
						.min(4, ' 4 ')
						.required('  '), 
	firstName: Yup.string()
					.typeError('  ')
					.min(2, '   ')
					.required('  '), 
	lastName: Yup.string()
					.min(2, '   ')
					.required('  '),
	approved: Yup.boolean()	
});


export let theftMessageSchema = Yup.object({
	licenseNumber: Yup.string().required('  '),
	ownerFullName: Yup.string()
									.min(6, '   ')
									.required('  '),
	type: Yup.string().required(' '),								
	color: Yup.string().nullable(),
	date: Yup.date()
					.nullable()
					.required(' '),
	officer: Yup.string(),				
	description: Yup.string().nullable()
});


export let theftRecordSchema = Yup.object({
	status:  Yup.string().required(' '),
	licenseNumber: Yup.string().required('  '),
	type: Yup.string().required(' '),
	ownerFullName: Yup.string()
									.min(6, '   ')	
									.required('  '),
	clientId: Yup.string(),
	createdAt: Yup.date().nullable(),
	updatedAt: Yup.date().nullable(),
	color: Yup.string().nullable(),
	date: Yup.date(),
	newTheftDate: Yup.date().nullable(),
	description: Yup.string().nullable(),
	resolution: Yup.string()
								.nullable()
								.when('status', {
									is: 'done',
									then: Yup.string().nullable().required(' ')
								}),
	officer: Yup.string().nullable()
});